<template lang="pug">
.BoundarySelector
    select(v-model="selectedBoundary")
        option(value="") {{ allLabel }}
        option(v-for="item in boundaryIds") {{ item }}

</template>

<script>
// const stats = require('@/assets/outdata/stats.json');
// const spots = require('@/assets/outdata/spots.json').features; // TODO more efficient loading

export default {
    name: "BoundarySelector",
    props: [
        'boundaryType',
        'boundaryIds',
        'allLabel'
    ],
    data: () => ({
        selectedBoundary: ""
    }),
    watch: {
        selectedBoundary() {
            window.Map.filterBoundaryType = this.boundaryType;
            window.Map.filterBoundaryId = this.selectedBoundary;
            window.Map.selectedSpotId = null;
            window.SpotInfo.spot = null;
        }
    },
    created() {
        window.BoundarySelector = this;
    }
}
</script>

<style scoped>
option {
    text-transform:capitalize;
}

</style>