            label(for="option-wara") WA Regional Area
                //- BoundarySelector(allLabel="All locations" :boundaryIds="boundaryIds('wara')" boundaryType='wara' ref="wara")
                DownloadLink(v-if="selected === 'wara'")
        .option
            input#option-perth(type="radio" value="perth" v-model="selected") 
            label(for="option-perth") Perth Metro Area 
                //- BoundarySelector(allLabel="All locations" :boundaryIds="boundaryIds('perth')" boundaryType='perth' ref="perth")
                DownloadLink(v-if="selected === 'perth'")
        .option
            input#option-walgas(type="radio" value="walgas" v-model="selected") 
            label(for="option-walgas") Local Council Areas
                BoundarySelector(allLabel="All councils" :boundaryIds="boundaryIds('walgas')" boundaryType='walgas' ref="walgas")
                DownloadLink(v-if="selected === 'walgas'")
        .option
            input#option-ssc(type="radio" value="ssc" v-model="selected") 
            label(for="option-ssc") Suburbs
                BoundarySelector(allLabel="All suburbs" :boundaryIds="boundaryIds('ssc')" boundaryType='ssc' ref="ssc")
                DownloadLink(v-if="selected === 'ssc'")
        .option
            input#option-waced(type="radio" value="waced" v-model="selected") 
            label(for="option-waced") Federal Electorates
                BoundarySelector(allLabel="All electorates" :boundaryIds="boundaryIds('waced')" boundaryType='waced' ref="waced")
                DownloadLink(v-if="selected === 'waced'")
        .option
