            label(for="option-waseb") State electorates
                BoundarySelector(allLabel="All electorates" :boundaryIds="boundaryIds('waseb')" boundaryType='waseb' ref="waseb")
                DownloadLink(v-if="selected === 'waseb'")



</template>

<script>
import BoundarySelector from '@/components/BoundarySelector.vue';
import DownloadLink from '@/components/DownloadLink.vue';

export default {
    name: "BoundaryType",
    data: () => ({
        selected: "",
        stats: Object.freeze(require('@/assets/outdata/stats.json'))
    }),
    components: {
        BoundarySelector,
        DownloadLink
    },
    methods: {
        boundaryIds(boundaryType) {
            return Object.keys(this.stats[boundaryType]).sort();
        }
         
    },
    created() {
        window.BoundaryType = this;
    }, watch: {
        selected(type) {
            window.Map.filterBoundaryType = type;
            if (this.$refs[type]) {
                window.Map.filterBoundaryId = this.$refs[type].selectedBoundary;
            }
            window.Map.selectedSpotId = null;
            window.SpotInfo.spot = null;
        }
    }
}
</script>

