<template lang="pug">
    #map
</template>

<script>
import mapboxgl from 'mapbox-gl';
import U from 'mapbox-gl-utils';
import boundaryBounds from '@/bounds/bounds.json';

export default {
    data: () => ({
        filterBoundaryType: undefined,
        filterBoundaryId: undefined,
        selectedSpotId: undefined
    }),
    // methods: {
    //     filterLGA() {

    //     }
    // },
    mounted() {
        mapboxgl.accessToken = 'pk.eyJ1Ijoic3RldmVjcm93ZHNwb3QiLCJhIjoiY2p0ZHdkOTI5MDN4ZTQ1bWhheXhnOXZxZiJ9.TSlGIBLdCQTfDq-vr0B8dg';
        const devStyle = { version: 8, sources: {}, layers: [] };
        const map = new mapboxgl.Map({
            container: 'map',
            center: [115, -30],
            zoom: 5,
            minZoom: 4,
            // style: devStyle
            // style: 'mapbox://styles/mapbox/light-v9',
            style: 'mapbox://styles/stevecrowdspot/cjtexexbm5cej1fpm9zn9l4ft',
        });
        U.init(map);
        window.map = map;
        window.Map = this;
        map.U.onLoad(() => {
            initMap(map);
            this.$emit('loaded');
        });
    }, watch: {
        filterBoundaryType(boundaryType, prevBoundaryType) {
            if (prevBoundaryType) {
                map.U.hide([`${prevBoundaryType}-line`,`${prevBoundaryType}-highlight`]);
            }
            if (boundaryType) {
                map.U.show([`${boundaryType}-line`,`${boundaryType}-highlight`]);
            }
            if (boundaryType === 'perth') {
                this.filterBoundaryId = 'Perth';
                this.$nextTick(() => this.updateFilterBoundaryId('Perth'));
            } else if (boundaryType === 'wara') {
                this.filterBoundaryId === 'Regional';
                this.$nextTick(() => this.updateFilterBoundaryId('Regional'));
            }

        },
        filterBoundaryId(id) {
            this.updateFilterBoundaryId(id)
        }, selectedSpotId(id) {
            if (id) {
                map.U.setFilter('spots-highlight', ['==', 'id', id]);
            } else {
                map.U.setFilter('spots-highlight', false);
            }

        }
    }, methods: {
        highlight(point) {
            // TODO nicer movement
            map.panTo(point.geometry.coordinates, { zoom: 13 });
        }, updateFilterBoundaryId(id){
            const idField = boundaryType => ({
                ssc: 'SSC_NAME16',
                walgas: 'wa_lga_s_3',
                waseb: 'WA_STATE_2',
                wara: 'CC_1',
                perth: 'NAME_2',
                waced: 'Elect_div'
            })[boundaryType];

            let spotFilter = true, boundaryFilter = false;
            if (id && this.filterBoundaryType) {
                if (this.filterBoundaryType === 'perth') {
                    spotFilter = ['!=', ['get', this.filterBoundaryType], 'Outside Perth'];
                    boundaryFilter = true;
                } else if (this.filterBoundaryType === 'wara') {
                    spotFilter = ['==', ['get', this.filterBoundaryType], 'Regional'];
                    boundaryFilter = true;

                } else {
                    spotFilter = ['==',  ['get', this.filterBoundaryType], id];
                    boundaryFilter = ['==', ['get', idField(this.filterBoundaryType)], id];
                }
                
            }
            map.U.onLoad(() => {
                map.U.setFilter('spots-circles', spotFilter);
                map.U.setFilter(`${this.filterBoundaryType}-highlight`, boundaryFilter);

                map.fitBounds(boundaryBounds[this.filterBoundaryType][id]);
            });

        }
    }
}



function initMap(map) {
    const boundaryTypes = ['waced', 'perth', 'waseb', 'wara', 'ssc', 'walgas'];
    const tileHost = 'http://localhost:5005';
    const tileEnv = window.location.host.match('localhost') ? 'localhost' : 'mapbox';
    const mapboxIds = {
        waced: 'stevecrowdspot.bxlemukf',
        walgas: 'stevecrowdspot.5pbf0wnf',
        waseb: 'stevecrowdspot.als9poou',
        perth: 'stevecrowdspot.6lg5dj5y',
        wara: 'stevecrowdspot.7rksc9ev',
        ssc: 'stevecrowdspot.7pm3zdfb',
        spots: 'stevecrowdspot.6zf6czzd'
    }

    for (const boundaryType of boundaryTypes) {
        let url;
        if (tileEnv !== 'mapbox') {
            url = `${tileHost}/tiles/${boundaryType}/index.json`;
        } else {
            url = `mapbox://${mapboxIds[boundaryType]}`;
        }
        map.U.addVector(`${boundaryType}`, url)
        .addLine(`${boundaryType}-line`, {
            lineColor: 'lightgrey',
            sourceLayer: boundaryType,
            visibility: 'none'
        });
    }

    // map.U.addGeoJSON('/outdata/spots.json')
    map.U.addVector('spots', tileEnv === 'mapbox' ? `mapbox://${mapboxIds.spots}` : `${tileHost}/tiles/spots/index.json`)
    .addCircle('spots-circles', {
        sourceLayer: 'spots',
        circleColor: 
            ['case',
                ['to-boolean', ['feature-state', 'hover']],
                ['interpolate-hcl', ['linear'], ['get', 'supports'],
                    0, 'hsl(120, 100%, 70%)',
                    5, 'hsl(30, 100%, 60%)',
                    50, 'hsl(0, 100%, 60%)'
                ],    
                ['interpolate-hcl', ['linear'], ['get', 'supports'],
                    0, 'green',
                    5, 'orange',
                    50, 'red'
                ],
            ]
        ,
        // circleColor: [
        //     'interpolate', ['linear'], ['get', 'supports'],
        circleRadius: {
            stops: [
                [4, 3],
                [8, 5],
                [12, 10]
            ]
        },
        circleOpacity: {
            stops: [
                [8, 0.6],  
                [13, 0.9]
            ]
        },
        circleStrokeWidth: 
            ['interpolate', ['linear'], ['zoom'],
                8,
                    ['case',
                        ['to-boolean', ['feature-state', 'hover']],
                            1,
                            0
                    ],
                14, ['case',
                        ['to-boolean', ['feature-state', 'hover']],
                            4,
                            2
                    ]
            ]
        ,
        circleStrokeColor: {
            property: 'supports',
            stops: [
                [0, 'darkgreen'],
                [5, 'darkorange'],
                [10, 'darkred'],
            ],
            colorSpace: 'hcl'
        },

        filter: true
    }).addCircle('spots-highlight', {
        sourceLayer: 'spots',
        circleColor: 'transparent',
        // circleStrokeColor: 'hsl(45.7, 98.1%, 59%)',
        // circleStrokeColor: 'hsl(45, 100%, 70%)',
        circleStrokeColor: 'hsl(215.2, 41.9%, 35.1%)',
        circleRadius: 30,
        circleStrokeWidth: 3,
        filter: false
    });

    for (const boundaryType of boundaryTypes) {
        map.U.addLine(`${boundaryType}-highlight`, boundaryType, {
            lineColor: 'darkred',
            lineWidth: 3,
            sourceLayer: boundaryType,
            filter:false,
            lineJoin: 'round',
            lineCap: 'round'
        })
    }

    map.U.hoverPointer('spots-circles');
    map.U.hoverFeatureState('spots-circles', 'spots', 'spots');

    map.U.clickLayer('spots-circles', e => {
        const p = e.features[0].properties;

        console.log(p);
        window.SpotInfo.spot = p;

        window.Map.selectedSpotId = p.id;
    });
    map.addControl(new mapboxgl.NavigationControl(), 'top-right');

}


import 'mapbox-gl/dist/mapbox-gl.css';
</script>

<style scoped>
#map {
    width: 100%;
    height: 100%;
    position:absolute;
}
</style>