</template>

<script>
export default {
    name: "DownloadLink",
    data: () => ({
        
    }), computed: {
        csvName() {
            // nervous about this being updated at the correct time
            let boundaryId = 'All';
            if (window.Map.filterBoundaryType === 'wara') {
                boundaryId = 'Regional';
            } else if (window.Map.filterBoundaryType === 'perth') {
                // the 'All' dataset contains everything that is not 'Outside Perth'
                // boundaryId = 'Perth';
            } else if (window.Map.filterBoundaryId !== '') {
                boundaryId = window.Map.filterBoundaryId;
            }
            
            return `csv/${window.Map.filterBoundaryType}/${boundaryId.replace(/[ ]/g, '_')}.csv`;
        }
    },
    created() {
        window.DownloadLink = this;
    }
}
</script>

